body, html {
  background-color: #45485B;
  margin: 0;
  padding: 0;
  position: relative;
  width: 100%;
  overflow: hidden;
  height: 100%;
  font-family: TT Norms;
}

.app {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.button {
  position: relative;
  height: 100%;
  overflow: hidden;
}
.button__icon {
  transition: all 1.2s ease-in;
  opacity: 0;
}
.button__icon--start {
  opacity: 1;
}
.button-list {
  padding: 0 1%;
  transition: all ease-in-out 0.3s;
  transition-delay: 0.1s;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 1%;
}
.button__label {
  transition: ease-in-out all 0.4s;
  font-size: 2.4vw;
  position: absolute;
  top: 60%;
  alignment-baseline: top;
  left: 0;
  right: 0;
  margin: auto;
  display: block;
  width: 100%;
  text-align: center;
  color: rgb(255, 255, 255);
  text-transform: uppercase;
}
.button__window {
  transition: all ease-in-out 0.4s;
  overflow: hidden;
  position: relative;
  display: flex;
  width: 100%;
  box-sizing: border-box;
  height: 100%;
}
.button__icon-wrapper {
  width: 70%;
  position: absolute;
  top: 45%;
  transform: translateY(-50%);
  right: 0;
  left: 0;
  margin: auto;
}
.button__icon-wrapper > img {
  transition: ease-in-out opacity 0.4s;
  position: absolute;
  right: 0;
  top: 50%;
  width: 100%;
  transform: scale(1) translateY(-60%);
}
.button--active .button__label {
  color: #7cb596 !important;
}
.button--active .button__window {
  background: none;
}
.button--active .button__icon--select {
  opacity: 1 !important;
  transform: scale(1) translateY(-60%);
}
.button--unfocused .button__icon {
  opacity: 0;
}
.button--unfocused .button__icon--unfocused {
  opacity: 1;
}

.panel--active .button-list {
  transform: translateY(-22vh);
}
.panel--active .button__label {
  color: #4FA4DA;
}
.panel--active .decor__column:before {
  opacity: 0;
}
.panel--active .decor__label {
  color: #4FA4DA;
}

.footer {
  transition: all ease-in-out 0.2s;
  display: grid;
  grid-template-rows: 1fr auto;
  flex-direction: column;
  vertical-align: center;
  justify-content: center;
  position: absolute;
  width: 100%;
  height: 40vh;
  margin-bottom: -100%;
  background: linear-gradient(16deg, #A3C45C 0%, #A0C361 9%, #96BF70 21%, #85B989 33%, #6EB0AB 46%, #51A5D6 59%, #4FA4DA 60%);
}
.footer--show {
  margin-bottom: 0;
  bottom: 0;
}
.footer__item-list {
  width: 90%;
  margin: 2% auto;
  font-family: "TT Norms";
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 2%;
  color: #45485B;
  font-weight: bold;
  font-size: 1.7vw;
  align-items: center;
}
.footer__item {
  display: grid;
  gap: 5%;
  grid-template-columns: auto 1fr;
}
.footer__description {
  margin-top: 10%;
}
.footer__step-counter {
  align-self: start;
  float: left;
}
.footer__step-counter svg {
  height: 5vw;
}
.footer__button-close {
  background: linear-gradient(46deg, #A3C45C -164.24%, #A0C361 -124.22%, #96BF70 -75.66%, #85B989 -22.63%, #6EB0AB 33.55%, #51A5D6 91.71%, #4FA4DA 96.02%, #4FA4DA 114%, #4FA4DA 266.54%, #4FA4DA 266.58%);
  width: 20%;
  height: 6vh;
  border-radius: 50px;
  font-size: 2vw;
  text-transform: uppercase;
  stroke-width: 1px;
  border: 1px solid #FFF;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  color: #45485B;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin: 0 auto 3% auto;
}

.decor {
  position: absolute;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 1%;
  padding: 0 1%;
  box-sizing: border-box;
}
.decor__column {
  position: relative;
  border: 1px solid rgb(50, 52, 65);
}
.decor__column.active {
  background: #323441;
}
.decor__column:before {
  transition: all ease-in-out 0.4s;
  opacity: 1;
  content: " ";
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  position: absolute;
  background: linear-gradient(0deg, #79b39b 0, #57a7cc 14%, #4d8cb9 28%, #486786 45%, #45485b 88%, #45485b);
}

.popup {
  width: 100%;
  height: 100%;
  position: relative;
  background-color: rgba(0, 0, 0, 0.32);
  z-index: 99999;
  display: none;
}
.popup__show {
  display: flex;
}
.popup__content {
  font-size: 5vw;
  font-weight: 100;
  padding: 2%;
  width: auto;
  margin: auto;
  height: auto;
  background-color: white;
}