.popup{
  width: 100%;
  height: 100%;
  position: relative;
  background-color: rgba(0, 0, 0, 0.32);
  z-index: 99999;
  display: none;

  &__show{
    display: flex;
  }

  &__content{
    font-size: 5vw;
    font-weight: 100;
    padding: 2%;
    width: auto;
    margin: auto;
    height: auto;
    background-color: white;
  }
}