.footer{
  transition: all ease-in-out 0.2s;
  display: grid;
  grid-template-rows: 1fr auto;
  flex-direction: column;
  vertical-align: center;
  justify-content: center;
  position: absolute;
  width: 100%;
  height: 40vh;
  margin-bottom: -100%;
  background: linear-gradient(16deg, #A3C45C 0%, #A0C361 9%, #96BF70 21%, #85B989 33%, #6EB0AB 46%, #51A5D6 59%, #4FA4DA 60%);

  &--show{
    margin-bottom: 0;
    bottom: 0;
  }

  &__item-list{
    width: 90%;
    margin:  2% auto;
    font-family: "TT Norms";
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2%;
    color: #45485B;
    font-weight: bold;
    font-size: 1.7vw;
    align-items: center;
  }

  &__item{
    display: grid;
    gap: 5%;
    grid-template-columns: auto 1fr ;
  }

  &__description{
    margin-top: 10%;
  }

  &__step-counter{
    align-self: start;
    float: left;

    svg{
      height: 5vw;
    }
  }

  &__button-close{
    background: linear-gradient(46deg, #A3C45C -164.24%, #A0C361 -124.22%, #96BF70 -75.66%, #85B989 -22.63%, #6EB0AB 33.55%, #51A5D6 91.71%, #4FA4DA 96.02%, #4FA4DA 114%, #4FA4DA 266.54%, #4FA4DA 266.58%);
    width: 20%;
    height: 6vh;
    border-radius: 50px;
    font-size: 2vw;
    text-transform: uppercase;
    stroke-width: 1px;
    border: 1px solid #FFF;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    color: #45485B;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0 auto 3% auto;
  }
}