.decor{
  position: absolute;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 1%;
  padding: 0 1%;
  box-sizing: border-box;

  &__column{
    position: relative;

    border: 1px solid rgba(50, 52, 65, 1);

    &.active{
      background: #323441;
    }

    &:before{
      transition: all ease-in-out 0.4s;
      opacity: 1;
      content: " ";
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      position: absolute;
      background: linear-gradient(0deg,#79b39b 0,#57a7cc 14%,#4d8cb9 28%,#486786 45%,#45485b 88%,#45485b);
    }
  }
}

