.button{
  position: relative;
  height: 100%;
  overflow: hidden;

  &__icon{
    transition: all 1.2s ease-in;
    opacity: 0;

    &--start{
      opacity: 1;
    }
  }

  &-list{
    padding: 0 1%;
    transition: all ease-in-out 0.3s;
    transition-delay: 0.1s;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 1%;
  }

  &__label{
    transition: ease-in-out all 0.4s;
    font-size: 2.4vw;
    position: absolute;
    top: 60%;
    alignment-baseline: top;
    left: 0;
    right: 0;
    margin: auto;
    display: block;
    width: 100%;
    text-align: center;
    color: rgba(255, 255, 255, 1);
    text-transform: uppercase;
  }

  &__window{
    transition: all ease-in-out 0.4s;
    overflow: hidden;
    position: relative;
    display: flex;
    width: 100%;
    box-sizing: border-box;
    height: 100%;
  }

  &__icon-wrapper{
    width: 70%;
    position: absolute;
    top: 45%;
    transform: translateY(-50%);
    right: 0;
    left: 0;
    margin: auto;

    &>img{
      transition: ease-in-out opacity 0.4s;
      position: absolute;
      right: 0;
      top: 50%;
      width: 100%;
      transform: scale(1) translateY(-60%);
    }
  }

  &--active{
    .button{
      &__label{
        color: #7cb596 !important;
      }

      &__window{
        background: none;
      }

      &__icon{
        &--select{
          opacity: 1 !important;
          transform: scale(1) translateY(-60%);
        }
      }
    }
  }

  &--unfocused{
    .button{
      &__icon{
        opacity: 0;

        &--unfocused{
          opacity: 1;
        }
      }
    }
  }
}

.panel{
  &--active{
    .button-list{
      transform: translateY(-22vh);
    }

    .button{
      &__label{
        color: #4FA4DA;
      }
    }

    .decor{
      &__column{
        &:before{
          opacity: 0;
        }
      }

      &__label{
        color: #4FA4DA;
      }
    }
  }
}



